import { Outlet } from 'react-router-dom';
import { JumboList, LinkItem } from '../components/JumboList';
import webdevPic from '../img/webdev.jpg';
import rFxPic from '../img/renamerfx.jpg';
import dDesignPic from '../img/ddesign.png';
import linuxPic from '../img/linuxexperience.jpg'

const entries = [
  new LinkItem('/portfolio/webdev', 'Web Development', webdevPic),
  new LinkItem('/portfolio/renamerfx', 'RenamerFX', rFxPic),
  new LinkItem('/portfolio/linux-design', 'Desktop Design', dDesignPic),
  new LinkItem('/portfolio/linux-experience', 'Linux Experience', linuxPic),
];

export function PortfolioList() {
  return (
    <JumboList items={entries} height='h-48' />
  );
}

export function Portfolio() {
  return (
    <div>
      <Outlet />
    </div>
  );
}