import BlogFrame from '../../components/BlogFrame';
import fedora from "../../img/fedora.png";
import suse from "../../img/opensuse.png";
import ubuntu from "../../img/ubuntu.png";

const text = (
  <div>
    <h1>My Long GNU/Linux Experience</h1>
    <p>I've been using Linux in different forms for so long I feel like it counts as a project. Especially since the "year of the desktop Linux", which is perpertually just around the corner for the third decade now, just isn't happening, so this is <i>still</i> an odd tech thing to do, but also one with great real-world applications.</p>
    <p>The full list of distros I've used in somewhat chronological order is the following:</p>
    <p><i>Best Linux 2000, Red Hat Linux, Mandrake, ELKS, Puppy, Ubuntu, Debian, Yellow Dog Linux, Fedora Core, openSUSE, Korora, Arch, Gentoo, CentOS, Raspbian, Oracle</i></p>
    <p>Each with a story of its own. I'll focus on my main distros below.</p>

    <h2>Fedora</h2>
    <div className="ico">
      <img className="ico" src={fedora} alt="Fedora logo"/>
    </div>
    <p>My distro of choice since Fedora 18. No distro-hopping since jumping ship from Ubuntu. <a href="http://mattdm.org/fedora/next/#1" target="_blank" rel="noopener noreferrer">Fedora.Next</a> seems like excellent initiative and F22 will be amazing with Wayland and KDE Plasma 5 defaults (Plasma in Fedora KDE Spin, obviously). Update: F22 only utilizes Wayland as default on GNOME login screen. While it has GNOME Wayland option, it is not yet default. GNOME is shaping up to be awesome in its own way as well with improved notifications and further refined applications. Update: In fact, both GNOME 3.16 and Plasma 5.3, which both shipped with F22, are so good that I have a hard time deciding which one to use as my daily desktop.</p>
    <p>GNOME 3.16 had fixed three of my biggest grievances: very slow animation speeds (no more need for Impatience shell extension), tendency to stutter with many windows when entering Activities if dynamic desktops are enabled (now I can use them yay) and notification/tray area in the bottom. Window titles of default Adwaita on traditional applications seems to be a bit slimmer as well, so I don't have urge to quickhack metacity anymore. Not that I care much for GNOME nowadays, but still this is a excellent release. I have never been this excited about a single Linux distro version release before, and I've been using these things for a decade. In the end F22 really did deliver. This is just fantastic release.</p>
    <p><a href="http://rpmfusion.org/Configuration" target="_blank" rel="noopener noreferrer">RPMFusion</a> is <em>the</em> essential extra repository for Fedora users, having packages that enable playback of pretty much any video format and other not-so-libre software.</p>
    <p><a href="http://copr.fedoraproject.org/" target="_blank" rel="noopener noreferrer">Fedora COPR's</a> are similar to Ubuntu PPA's, just easier to enable and centralized into one searchable place. Someone in IRC compared COPR's to Arch AUR, but that's not really the case in my opinion.</p>
    <p>Fedora has recently (2021) done something very interesting with <a href="http://copr.fedoraproject.org/" target="_blank" rel="noopener noreferrer">Fedora Silverblue</a>, which has a read-only root filesystem image as its base. It relies on Flatpaks and rpm-ostree for any additional software installations user wants to build atop it. Basically it guarantees you can't bork your system, while all base system updates come as full update images.</p>

    <h2>openSUSE</h2>
    <div className="ico">
      <img className="ico" src={suse} alt="openSUSE logo"/>
    </div>
    <p>Old favorite of mine. I used it extensively from 2009 to 2012. Main strengths of openSUSE are technical, shipping with extremely good package manager zypper and YaST for configuring pretty much anything under the hood with a nice GUI (think Windows Control Panel). It also has the greatest KDE implementation of major distributions IMHO (arguably Chakra has the best one).</p>
    <p>Yet it isn't perfect. The default repositories are quite small so I had six extras setup at one point which really slowed down every package action, sometimes their security policies were <a href="http://forums.opensuse.org/showthread.php/467942-networkmanager-requires-root-privileges-2" target="_blank" rel="noopener noreferrer">absolutely bonkers</a> and so forth. But generally it's stable and robust.</p>

    <h2>Ubuntu</h2>
    <div className="ico">
      <img className="ico" src={ubuntu} alt="Ubuntu logo"/>
    </div>
    <p>The distro that almost everyone starts with. However, I can not recommend this distribution or any of its many derivatives to anyone, especially not for new users. There are things that are done horribly wrong in this distribution on the most fundamental level. Or if you insist on using it yourself despite the numerous obvious problems you must have encountered during your time with it, at least do not recommend it to others.</p>
    <p>Some people like also complain that Canonical is hostile to good of greater community, as they often do their own thing with little regard to "bigger picture" of entire Linux ecosystem. I don't think like that. If they want to go their own way in matters of desktop, init system or whatever, it's their call and they are free to do so. Hell, for a short while their Upstart really <em>was</em> the technically best init system for Linux systems! And it was adopted by others! Let's say against all odds Mir really ends up being technically superior to Wayland. Then the initial power struggle between the two display managers will ultimately be a good thing. Their <span title="Insisting on doing duplicate work instead of contributing to existing projects">NIH Syndrome</span> is their own choice.</p>
    <h2>Worthy for Portfolio?</h2>
    <p>It was surprising to see during my university years how common it is that computer science or engineering students have no Linux skills. Even as late as my final advanced courses, I encountered people who had no idea how command-line works. To my surprise, these skills and experiences are not a given even among the most well-versed technical people.</p>
    <footer>2015-06-06 (edited: 2022-02-15)</footer>
  </div>
);

export default function LinuxExperience() {
  return (
    <BlogFrame text={text} />
  );
}