import { LinkItem, JumboList } from "../components/JumboList";

import aboutPic from '../img/about-sea.jpg';
import portfolioPic from '../img/portfolio-taulu.jpg';
import writingsPic from '../img/writings-txt.jpg';


const mainPages = [
  new LinkItem('/about', 'About', aboutPic),
  new LinkItem('/portfolio', 'Portfolio', portfolioPic),
  new LinkItem('/writings', 'Writings', writingsPic),
];

export default function Home() {
  return (
    <JumboList items={mainPages} height='h-64' />
  );
}