import BlogFrame from '../../components/BlogFrame';
import kdelogo from "../../img/kde.png";
import gnomelogo from "../../img/gnome.png";
import adwasmal from "../../img/adwasmal-headerbar.png";
import trayresize from "../../img/tray-transform.png";
import homerun0 from "../../img/homerun0.png";
import homerun3 from "../../img/homerun3.png";
import undo1 from "../../img/undo1.png";
import undo2 from "../../img/undo2.png"
import redo from "../../img/redo.png";
import stupid from "../../img/breeze-icon-blunder.jpg";

const kde = (
  <div>
    <h1>KDE Visual Design Group participation</h1>
    <div className="clearfix">
      <div className="ico">
        <img src={kdelogo} alt="[KDE logo]" />
      </div>
      <p>I was contributing to <a href="https://vdesign.kde.org/" target="_blank" rel="noopener noreferrer">KDE Visual Design Group</a> around its inception in summer 2013. Purpose of the VDG was to create a new uniform style for the new KDE major version. This became known as Breeze, replacing the old Oxygen visual. I was providing ideas, contributing mock-ups and giving feedback. KDE Plasma 5 started shipping in major Linux distriburions in early 2015. None of my suggestions ended up shipping.</p>
    </div>
    <div className="clearfix">
      <h2 className="clear-left">Smaller tray icons experiment</h2>
      <p>KDE has tray icons which are as large as regular buttons. Unless user takes care in hiding most of them by default, it can become extremely long and messy. I made mockups showing how much horizontal space would be liberated if tray icons were just three pixels smaller vertically.</p>
      <div>
        <img src={trayresize} alt="[Tray transformation process]" title="Whole process in one picture." />
      </div>
      <p>From top:</p>
      <p>1) Initial tray area with shortcuts smashed right next to it. 2) Areas colored with tray in purple, shortcuts in green and menu in yellow. 3) Slightly vertically smaller tray. 4) Tray up-arrow removed since it was kinda redundant in Plasma 5.</p>
      <p>Last two are tray area before/after with alpha channel left intact to show how much more horizontal space is gained with only three pixels removed from vertical size of a tray icon.</p>
    </div>
    <div>
      <h2>Refining Homerun launcher</h2>
      <p>Homerun launcher is by far the best main menu option for KDE, taking inspiration from the classic menu of KDE. I'd love to see it as polished as possible. It also should be the KDE default, not just inbuilt alternative. The KDE's default menu called "Kickoff" requires too many clicks.</p>
      <div className="grid md:grid-cols-2 md:gap-4 gap-2">
        <div>
          <img src={homerun0} alt="[Original Homerun launcher]" title="Always room for improvement!" />
          <p>The original Homerun launcher. Geometry is off, and session control buttons are mashed together with favorite application shortcuts.</p>
        </div>
        <div>
          <img src={homerun3} alt="[My take on Homerun]" title="I think this is pretty good. Lightened up the colour to make it more appealing." />
          <p>My take on it. Edits to original screenshot were made with Kolourpaint.</p>
        </div>
      </div>
    </div>
    <div className="clearfix">
      <h2>Plasma Breeze undo/redo buttons</h2>
      <p>Five minutes in Kolourpaint after inspiration. There was discussion about undo/redo buttons not really conveying any meaning in their current simple curved arrows form. Question was revolving around how to <i>really</i> separate their visual semantics from back/forward icons, which also are just arrow.</p>
      <p>This was my take on the issue:</p>
      <div className="grid sm:grid-cols-3 md:gap-14 sm:gap-8 gap-2 items-center">
        <div>
          <img src={undo1} alt="[Undo]" title="Undo" />
          <p>Undo. "No, this change was bad, cross over it and go back"</p>
        </div>
        <div>
          <img src={undo2} alt="[Undo2]" title="Undo alternative" />
          <p>Undo v2. Now there actually is "it" that is crossed over.</p>
        </div>
        <div>
          <img src={redo} alt="[Redo]" title="Redo" />
          <p>Redo. "Actually the change was a-ok, let's redo it"</p>
        </div>
      </div>
    </div>
    <div>
      <h2>KDE's Breeze icon duality mistake</h2>
      <a href="/" target="_blank"><img src={stupid} alt="[Icons changing how they look depending on size]" title="Icons changing how they look depending on size" /></a >
      <p>Breeze changes automatically how the icons look depending on their size. Change is drastic. Total break of consistency is a big no in interaction design. Consistency equals less user confusion. I've never been a fan of wireframe icons. Breeze's full icons are great. I reverted back to the good old Oxygen icons on KDE because of this.</p>
      <p>In KDE Visual Design Group's defense, the root cause to these icons - originally meant for small interfaces such as toolbars - ended up popping up in file navigation like that is in how KDE handles icons. There is difficulty in getting KDE to use the correct icon once size thresholds such as less than 24px trigger. I understand that the program can't deduce by itself what it should use.</p>
      <p>That said, the approach is clearly wrong. Keeping icons looking consistent no matter what size they are is the only correct approach. Those wireframe icons should never have been put into same icon set to avoid this problem.</p>
    </div>
    <footer>2015-05-26 (UPDATED: 2022-02-12)</footer>
  </div>
);

const gnome = (
  <div className="writing">
    <h1>GNOME's Adwasmal theme</h1>
    <div className="ico">
      <img className="ico rounded-lg" src={gnomelogo} alt="[GNOME logo]" />
    </div>
    <p>GNOME is clean, simple and most importantly it generally Just Works™. Then again GNOME suffers somewhat from over-simplification shooting itself in the head. Prime example is the mystery of suspend button. There is a neat row of three buttons which admittedly looks stylish: settings, lock and shutdown. None of those trigger suspend. Where is it? Hold down shift and shutdown button changes into pause button! Luckily GNOME has extensions which can be easily installed straight from website with two clicks, which remedy many of the problems. Another good thing is that generally development is moving to good direction with brisk pace: 3.0 was unusable, 3.2 was unusable, 3.4 was borderline unusable, 3.6 was slow and glitchy, but all releases beyond and including 3.8 have been perfectly usable and getting increasingly refined with each relese. Do note that only even numbered releases are considered stable.</p>
    <p>I tried to persuade GNOME designers to adapt my Adwaita tweak (Adwasmal) as the default window decoration for GNOME 3.14, as Adwaita was going to become the new GTK default theme, or at least make GTK2 titlebars smaller. I didn't succeed at that, since it didn't fit GNOME's design object and was hard to click they said (actually same size as Mist decoration). It can't be helped. I still think Adwaita GTK2 window decoration is absolutely bonkers. Why? Because it is the exact same size as the headerbar! But it only has window title and close button! Luckily, I think that </p>
    <div className="h-36">
      <img src={adwasmal} alt="Comparison between Adwasmal and Adwaita headerbar" />
    </div>
    <p>Adwasmal: Towards GTK3 headerbar being the same size as GTK2 headerbar <em>menu bar included</em>.</p>
    <p><s>Download Adwasmal for GNOME 3.10 <u>here</u>. Untar in home directory and enable in GNOME Tweak Tool. <strong>WARNING!</strong> It was a quick hack of metacity-2.xml for my own use, so <em>do not enable it on anything else except Window Decoration</em> or you'll get some pretty funky shenanigans.</s> Deprecated.</p>
    <p>As of GNOME 3.18, the titlebars are noticeably smaller, so no biggie. In general GNOME has evolved to be my preferred desktop environment. There are, however, still flaws which are rather aggravating.</p>
    <p>After a long period of using GNOME, the "exposé effect" that happens every time user presses meta starts adding to irritation leves. I'd much prefer my windows not floating around like synchronized swimming manatees, thank you very much. It's cool at first, but like all unnecessarily cool things, it becomes an annoyance.</p>
    <p>I have disabled top left corner mouse-over actication completely and started using the window list at the bottom, as well as classic menus. Maybe it's just how I grew up with computers, Windows XP and KDE teenager? Perhaps. However, I think this kind of excess <i>twitchiness</i> is simply bad design. For something so commonly done as switching application, the whole procedure is simply way too fancy for its own good. This should be a clear warning signal to developers that they are doing something wrong. Typically people grow into a new paradigm and get comfortable with it. For me, it was the opposite. I initially really liked how combined window switching and virtual desktops were done in GNOME 3... and worked around it.</p>
    <p>I doubt I'm far from only with feelings like this. Consider the following: CentOS does not ship with vanilla GNOME 3. It ships with classic mode as default, which adds the application and location menus from GNOME 2, plus that window list panel. Red Hat is smart company, and surely they have done UX testing before releasing very long term support desktop to people who work on it from nine to five. And they decided traditional window list is the way to go. Again, this tells plenty in my view.</p>
    <footer>2016-07-23</footer>
  </div>
);

export default function LinuxDesign() {
  return (
    <div>
      <BlogFrame text={kde} />
      <BlogFrame text={gnome} />
    </div>
  );
}