import BlogFrame from '../../components/BlogFrame';
import vuelogo from "../../img/vue-logo.png";
import html5logo from "../../img/html5.png";
import reactlogo from "../../img/react-logo192.png";
import vaadinlogo from "../../img/vaadin-logo.png";
import stevent from "../../img/stevent.png";

const text = (
  <div>
    <h1>Web Development</h1>
    <div className="clearfix">
      <h2>Homepage with React (2022)</h2>
      <div className="pfp">
        <img className="pfp" src={reactlogo} alt="reactlogo" />
      </div>
      <p>After React unveiled its new hooks-centric beta documentation, I decided to redo my homepage with it since my homepage was due for an update. I used <b>create-react-project</b> to spawn the project scaffolding, and decided to go with <b>Tailwind CSS</b> for responsive design, and of course add routing with <b>react-router-dom</b>.</p>
      <p>I took <b>mobile-first</b> thinking a step further. Aside from responsive changes such as number of columns and font size changes, the core interaction with the UI remains identical on every screen size. This reflects my "no surprises" philosophy for user interaction design: if user views the site first on mobile and then later on desktop, everything will still be in their right places.</p>
      <p>For the design, I drew my inspiration from video games: game menus and the in-game interfaces. Animations are there to augment this feel in addition to providing user feedback on action.</p>
      <footer>2022-02-19</footer>
    </div>

    <h2>Asset Manager with Vaadin (2021)</h2>
    <div className="clearfix">
      <div className="pfp">
        <img className="pfp" src={vaadinlogo} alt="vaadinlogo" />
      </div>
      <p>I joined a company developing a web-based asset management software as a full-stack engineer. Product was far from greenfield, and I had no control over technology decisions or design at any point. They had chosen to go with Vaadin as their choice for front-end framework.</p>
      <p>Over time I became the go-to "UI guy" of the team when something needed to get done on the front, especially when that something required getting things done with SCSS. During my time there I fixed, rewrote and implemented several UI components, such as custom date selectors, data grids, modals, range selectors, and data input windows.</p>
      <footer>2022-02-04</footer>
    </div>

    <h2>Homepage with Vue (2020)</h2>
    <div className="clearfix">
      <div className="pfp">
        <img className="pfp" src={vuelogo} alt="vuelogo" />
      </div>
      <p>It was made with Vue, vue-router and Bulma, and was hosted on GitHub. Project stub was spawned with vue-cli, just like Angular and React projects. I contemplated on doing it all by hand for the sake of properly learning, but I have already done one quick project like that to showcase that I can do things without all the NodeJS help by just putting script to head. Obviously end-result looked horrible given time constraints, and job application was rejected, so why do things the hard way when there is no prize to it?</p>
      <p>The basic idea was to take my old website and make it a modern responsive single-page application, which is exactly what I did. I looked into using React first because it's popular, but because Vue allowed me to quite straighforwardly copypaste all the old content into Vue's template tags and get it over with with very little else, I chose it. The whole transition from old to new was done over one weekend, complete with style overhaul, and axing code duplication by making components.</p>
      <p>Another reason to select Vue over React was its familiarity. I already knew basic web development, Angular and JavaFX for UI building, all which use the same clear separation of concerns into structure, style, and logic. Less mental overhead.</p>
      <p>Bulma was used to structure layout and make the site responsive. I was already familiar with Bootstrap, but chose Bulma because of it being CSS-only, while doing quite the same things as Bootstrap quite similarly. More precisely, Bulma delivered on the same easy column-based layout management we all know and love that I wanted. Perfectly adequate for a homepage. I made extensive use of its styles, too, only writing CSS myself when it was faster than reading how it's done with Bulma, or when Bulma's defaults needed overriding, which is thankfully easy to do without poisoning the project with !important mines.</p>
      <footer>2020-11-29 <i>(updated: 2022-02-04)</i></footer>
    </div>

    <h2>Event hosting platform with Angular (2017)</h2>
    <div className="clearfix">
      <img src={stevent} className="md:float-left md:mr-4 mb-4" alt="Stevent showcase banner" />
      <p>Capstone is a half year and 15 ECT university team project. Course took place in 2017. This is where I learned how to do the modern full-stack development.</p>
      <p>I was assigned into a startup simulator group, which was given freedom and responsibilty to do whatever as long as their product idea was approved by the course professors. <a href="https://staging.capstone.utu.fi/en-sevent" target="_blank" rel="noopener noreferrer">Stevent for Student Event</a>, was our winning pitch. I was the CTO of our 6 student team, responsible for deployment and making sure it worked.</p>
      <p>It was made with a MEAN stack of <b>MongoDB</b>, <b>Express</b>, <b>Angular</b> and <b>Node</b>. It was then deployed to <b>Heroku</b>. </p>
      <p>Stevent was advanced. It was a mobile-first SPA web application, which allowed user registration and logins via OAuth. It could use location data, then show user's location on Google Maps web view, on which user could put event notifications or join existing ones. Our team actually managed to pull it off as a completely usable product.</p>
      <p>Stevent is no longer developed. <a href="https://github.com/otjura/stevent" target="_blank" rel="noopener noreferrer">Its sources are at GitHub.</a></p>
      <footer>2017</footer>
    </div>

    <h2>My plain HTML website (2015)</h2>
    <div className="clearfix">
      <div className="pfp">
        <img className="pfp" src={html5logo} alt="html5logo" />
      </div>
      <p>Pure 100% hand-crafted HTML5 and CSS3, tested on latest Firefox, Opera and Chrome. <a href="https://otso.neocities.org" target="_blank" rel="noopener noreferrer">Take a look.</a> Initially I thought about just making a simple blog in WordPress, but then I found out about Neocities and decided to try my hand at making a simple website instead. Then it kinda "bloated" into what it is now. <span title="I should be sleeping but I'm toying with this (01:50:54)">This is actually pretty fun!</span></p>
      <p>Neocities is very limited in its capabilities, so modern full-stack web development with a fancy SPA is a practical impossibility. That said, constrained development is quite rewarding. Trying to make this look and behave like it's modern without any JavaScript is an exploration.</p>
      <p>My old website's landing page grid is the basis for my favicon. I didn't rip off Microsoft's Windows logo. I was a flat 4x4 square before them!</p>
      <footer>2017-04-11 <i>(updated: 2022-02-04)</i></footer>
    </div>
  </div>
);

export default function WebDev() {
  return (
    <BlogFrame text={text} />
  );
}